.vacancy-collection,
.directions{
  .experts-slider{
    @media screen and (min-width: 993px){
      .swiper{
        &-container{
          width: 100%;
        }
        &-wrapper{
          transform: unset !important;
          display: grid;
          gap: 1.25em;
          grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
        }
        &-slide{
          padding: 0;
          width: 100%;
        }
      }
    }
  }
}


.directions{
  .h2{
    text-align: right;
    &__row{
      @media screen and (min-width: 993px){
        &:nth-child(1){
          padding-right: 1.3em;
        }
        &:nth-child(2){
          padding-left: 0;
        }
      }
    }
  }

}

.direction-card{
  border-radius: 1.25em;
  background: rgba(235, 239, 240, 0.20);
  text-align: center;
  padding: 2.5em;
  height: 100%;
  display: flex;
  flex-direction: column;
  transition: all .3s;
  &:hover{
    background: #EBEFF0;
  }
  &__icon{
    height: 6.25em;
    @include center;
    img{
      height: 100%;
    }
  }
  &__badge{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $blue;
    color: $white;
    padding: 0 2.5em;
    height: 3.25em;
    border-radius: 31.25em;
  }
  &__nav{
    padding-top: 1.125em;
    margin-top: auto;
  }
}

._round{
  background: $primary;
  filter: blur(250px);
  border-radius: 50%;
  width: 25.625em;
  height: 25.625em;
  position: absolute;
  pointer-events: none;
}

._outline-round{
  position: absolute;
  border-radius: 50%;
  width: 116em;
  height: 69.625em;
  border: 1px solid $primary;
  transform: rotate(-22.002deg);
}

.consult{
  z-index: 2;
  color: $white;
  position: relative;
  overflow: hidden;
  ._outline-round{
    top: .5em;
    right: -33.25em;
    z-index: 2;
  }
  &__bg{
    @include imgCover2;
  }
  &__round{
    top: -7.75em;
    left: -6.8125em;
  }
  &__round2{
    right: -6.875em;
    bottom: -8.5em;
  }
  .container{
    position: relative;
    z-index: 3;
  }
}

.consult-form{
  &__btn{
    width: 100%;
  }
}


@media screen and (max-width: 992px){
  .directions{
    .h2{
      text-align: left;
    }
  }

  .direction-card{
    font-size: .75em;
    padding: 1.5em;
  }

  ._round{
    font-size: .5em;
  }

  .consult{
    ._outline-round{
      display: none;
    }
  }

  .consult-form{
    gap: .75em;
    &__btn{
      height: 4em;
      span{
        font-size: 1em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .direction-card{
    font-size: 1em;
  }
}
