.m-list {
  display: grid;
  grid-template-columns: 100%;
  gap: 1.25em;
  li {
    position: relative;
    font-size: 1.25em;
    line-height: 140%;
    letter-spacing: -0.02em;
    padding-left: 2.5em;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 1.5em;
      height: 1.5em;
      background-image: url("../img/m-list__icon.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
  }
}

.vacancy {
  position: relative;
  padding: 4.375em 0 1.875em;
  .container {
    position: relative;
    z-index: 2;
  }
  &__title {
    text-align: center;
    span {
      color: #E74C3D;
    }
  }
  &__wrap {
    position: relative;
    z-index: 2;
    display: flex;
    align-items: center;
    gap: 3.125em;
    padding-left: 8.125em;
    margin-top: 3.75em;
  }
  &__desc {
    max-width: 49.375em;
    flex-shrink: 0;
  }
  &__img {
    flex-grow: 1;
    img {
      width: 100%;
      border-radius: 1.25em 0 0 1.25em;
    }
  }
  .m-list{
    margin-top: 3.75em;
  }
  ._outline-round {
    z-index: 1;
  }
  ._outline-round2 {
    top: -4em;
    left: -33.875em;
    transform: rotate(14.877deg);
  }
  ._outline-round3 {
    top: 3em;
    right: -14em;
    transform: rotate(-20deg);
  }
  ._outline-round4 {
    top: 0;
    left: -46em;
  }
  ._outline-round5 {
    top: 20em;
    right: -22.25em;
    transform: rotate(40deg);
  }
}

@media screen and (max-width: 992px) {
  .m-list {
    gap: 0.625em;
    li {
      font-size: 1em;
      padding-left: 1.75em;
      &:after {
        width: 1.25em;
        height: 1.25em;
      }
    }
  }

  .vacancy {
    padding: 1.5em 0;
    &__wrap {
      gap: 1.25em;
      padding: 0 1em;
      margin-top: 1em;
    }
    &__desc {
      width: 50%;
      flex-shrink: unset;
    }
    &__img {
      width: 50%;
      img {
        border-radius: 1.25em;
      }
    }
    .m-list {
      margin-top: 1.25em;
    }
    ._outline-round {
      display: none;
    }
  }
}

@media screen and (max-width: 576px) {
  .vacancy {
    &__wrap {
      flex-direction: column;
    }
    &__desc,
    &__img {
      width: 100%;
    }
  }
}