.profit{
  min-height: 102.5em;
  position: relative;
  ._outline-round{
    top: 20.25em;
    right: -15.625em;
    transform: rotate(-32.642deg);
  }
  &__info{
    margin: 17.5em auto 0;
    width: 37.5em;
    height: 37.5em;
    background-color: $primary;
    color: $white;
    @include center;
    flex-direction: column;
    text-align: center;
    border-radius: 50%;
    img{
      margin-bottom: 1.25em;
      width: 18.75em;
    }
  }
  &__item,
  &__stat,
  &__round{
    position: absolute;
    width: 18.75em;
    height: 18.75em;
    background-color: #D9D9D9;
    border-radius: 50%;
  }
  &__round1{
    top: 9.25em;
    left: 36.125em;
  }
  &__round2{
    top: 12em;
    right: 30.75em;
  }
  &__round3{
    bottom: 33.75em;
    right: 16.375em;
  }
  &__round4{
    bottom: 17.125em;
    left: 37.125em;
  }
  &__stat{
    width: 21.875em;
    height: 21.875em;
    @include center;
    flex-direction: column;
    text-align: center;
    padding: 2.75em;
    &-desc{
      margin-top: .5em;
    }
  }
  &__stat1{
    top: 17.5em;
    left: 10em;
  }
  &__stat2{
    top: 25.75em;
    right: 10.75em;
  }
  &__stat3{
    bottom: 15em;
    right: 32.5em;
  }
  &__stat4{
    bottom: 13em;
    left: 8.25em;
  }
  &__item{
    color: $white;
    &-img{
      @include imgCover(100%);
      border-radius: 50%;
    }
    &-content,
    &:before{
      @include centerAbsolute;
    }
    &:before{
      content: '';
      width: calc(100% - 2.5em);
      height: calc(100% - 2.5em);
      border-radius: 50%;
      background: rgba(255, 255, 255, 0.20);
      backdrop-filter: blur(25px);
      z-index: 2;
      display: none;
    }
    &-content{
      @include center;
      flex-direction: column;
      text-align: center;
      z-index: 3;
    }
    &._blur{
      &:before{
        display: block;
      }
    }
  }
  &__item1{
    top: 43.75em;
    left: 14em;
  }
  &__item2{
    right: 8.25em;
    bottom: 8.75em;
    &:before{
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .profit{
    min-height: unset;
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 2em / 3));
    gap: 1em;
    ._outline-round{
      display: none;
    }
    &__round{
      display: none;
    }
    &__info,
    &__item,
    &__stat{
      width: 100%;
      height: auto;
    }
    &__stat{
      position: static;
    }
    &__item{
      position: relative;
      top: unset;
      left: unset;
      bottom: unset;
      right: unset;
    }
    &__info{
      border-radius: 0;
      grid-column: 1/-1;
      padding: 1em;
      position: static;
      margin: 0;
      img{
        width: 12em;
        margin: 0 0 1em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .profit{
    grid-template-columns: repeat(2, calc(50% - .25em));
    gap: .5em;
    padding: 2em .25em;
    &>*{
      font-size: .875em;
      min-height: 11.5em;
    }
    .profit__stat{
      padding: 1em;
    }
    .profit__info{
      border-radius: 50%;
      min-height: calc(100vw - .5em);
    }
    &__info,
    &__item,
    &__stat{

    }
  }
}