.stat{
  overflow: hidden;
  position: relative;
  .container{
    position: relative;
    z-index: 2;
  }
  .team-slider{
    margin-top: 8.75em;
    margin-bottom: 5em;
  }
  ._outline-round{
    top: 16.125em;
    left: -11.25em;
    transform: rotate(-19.477deg);
  }
}

.stat-item{
  &__title{
    @media screen and (min-width: 993px){
      font-size: 8.75em;
    }
  }
  &__desc{
    margin-top: .7em;
  }
}

.team-slider{
  &__desc{
    position: absolute;
    width: 55em;
    height: 55em;
    background: #E3EAEB;
    border-radius: 50%;
    padding: 9em;
    @include center;
    bottom: -5.3125em;
    right: -23.25em;
    z-index: 2;
    .h4{
      width: 9.35em;
      max-width: 100%;
    }
  }
  &__wrap{
    width: 50.25em;
  }
  .swiper{
    &-slide{
      width: 50%;
    }
    &-nav{
      padding-left: 63em;
    }
    &-button{
      position: static;
      margin: 0;
    }
  }
}

.team-card{
  &__img{
    border-radius: 1.25em;
    @include imgCover(122.25%);
  }
  &__info{
    margin-top: 1.25em;
    @include grid100gap(.625em);
  }
  &__desc{
    opacity: .5;
  }
}

.business{
  position: relative;
  z-index: 2;
  ._outline-round{
    top: 5em;
    left: -19em;
    transform: rotate(14.877deg);
  }
}

.business2{
  @media screen and (min-width: 993px){
    padding-bottom: 18.75em;
  }
}

.business-slider{
  &__wrap{
    overflow: hidden;
  }
  .round-btn{
    position: absolute;
    bottom: -9.375em;
    left: 50%;
    transform: translateX(-50%);
    z-index: 2;
  }
}

.business-card{
  display: grid;
  grid-template-columns: calc(100% - 48.75em) 47.5em;
  gap: 1.25em;
  padding: 1.25em;
  background-color: $white;
  border-radius: 1.25em;
  &__col{
    padding: 2.5em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: .625em;
    }
  }
  .text{
    margin-top: 1.25em;
    h6{
      opacity: .5;
    }
  }
}


@media screen and (max-width: 992px){
  .stat{
    ._outline-round{
      display: none;
    }
    .team-slider{
      margin-top: 3em;
      margin-bottom: 1em;
    }
    &__items{
      @include fullScroll;
      display: flex;
      padding-bottom: .5em;
      gap: 0;
    }
  }

  .stat-item{
    width: 14em;
    flex-shrink: 0;
    margin-right: 1em;
    &:last-child{
      margin-right: 0;
    }
  }

  .team-slider{
    &__desc{
      position: static;
      background-color: transparent;
      padding: 0;
      width: 100%;
      height: auto;
      margin: 0 0 1.5em;
      .h4{
        width: 100%;
      }
    }
    .swiper{
      &-slide{
        width: 19.5em;
      }
    }
  }
  
  .business{
    overflow: hidden;
    ._outline-round{
      display: none;
    }
  }

  .business-slider{
    &__wrap{
      overflow: unset;
    }
    .swiper{
      &-nav{
        position: static;
        margin: 1em 0;
        justify-content: center;
      }
    }
    .round-btn{
      position: static;
      margin: 1em 0;
      width: 100%;
      transform: unset;
    }
  }

  .business-card{
    grid-template-columns: 100%;
    gap: 1em;
    &__col{
      padding: 0;
    }
    &__col2{
      order: -1;
    }
  }
}


@media screen and (max-width: 576px){
  .business-card{
    padding: 1em;
    border-radius: .625em;
  }
}