.competence-team{
  position: relative;
  .container{
    position: relative;
    z-index: 2;
  }
  &:before{
    content: '';
    position: absolute;
    top: -2.5em;
    left: -14.5em;
    background-color: #E3EAEB;
    border-radius: 50%;
    width: 68.75em;
    height: 68.75em;
  }
  &__wrap{
    display: grid;
    grid-template-columns: 23.375em 51.25em;
    justify-content: space-between;
    gap: 1.25em;
  }
  &__items{
    @include grid100gap(2.5em);
  }
  &__item{
    display: flex;
    align-items: center;
    img{
      width: 3.75em;
      margin-right: 1.25em;
    }
    &-text{
      font-size: 1.5em;
      line-height: 140%;
      letter-spacing: -.02em;
    }
  }
}

.candidates{
  padding-bottom: 0;
  overflow: hidden;
}

.candidate-card{
  position: relative;
  color: $white;
  &__img{
    @include imgCover(129%);
    border-radius: 1.25em;
    &:after{
      background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 34%, #000 100%);
    }
  }
  &__content{
    z-index: 2;
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 2.5em;
    width: 100%;
    @include grid100gap(1.125em);
    text-align: center;
  }
  &__btn{
    width: 100%;
    &:not(:hover){
      color: $white;
    }
  }
}

.process,
.help{
  &__info{
    @include grid100gap(5em);
    position: relative;
    &:before{
      content: '';
      @include centerAbsolute;
      background: #E3EAEB;
      width: 66em;
      height: 66em;
      border-radius: 50%;
    }
  }
}

.help{
  padding-bottom: 12.5em;
  position: relative;
  ._outline-round{
    transform: rotate(-22.002deg);
    top: -31em;
    left: -49.25em;
  }
  &__inner{
    position: relative;
  }
  &__img{
    z-index: 3;
    position: absolute;
    width: 5em;
    height: 5em;
    transform: rotate(-15.564deg);
    img{
      border-radius: .625em;
      @include imgCover2;
    }
  }
  &__img1{
    top: 21.25em;
    left: 3.125em;
  }
  &__img2{
    top: 4em;
    left: 44.375em;
  }
  &__img3{
    top: 18.25em;
    left: 61.375em;
    transform: rotate(15.674deg);
  }
  &__img4{
    right: 13.25em;
    top: -.75em;
    transform: rotate(15.674deg);
  }
  .h2{
    position: relative;
    z-index: 2;
  }
  &__wrap{
    margin-top: 5.5em;
    display: grid;
    grid-template-columns: 23.375em 42.5em;
    justify-content: space-between;
    gap: 1.25em;
  }
  &__info{

    &-title,
    &-desc{
      position: relative;
      z-index: 2;
    }
    b{
      font-weight: 600;
    }
  }
  .m-list{
    gap: 2.5em;
  }
}


@media screen and (max-width: 992px){
  .competence-team{
    &:before{
      display: none;
    }
    &__wrap{
      grid-template-columns: 100%;
    }
    &__items{
      gap: .75em;
    }
    &__item{
      img{
        margin-right: .5em;
        width: 2.5em;
      }
      &-text{
        font-size: 1em;
      }
    }
  }

  .candidates-slider{
    .swiper{
      &-slide{
        width: 19.5em;
      }
      &-nav{
        display: none;
      }
    }
  }

  .candidate-card{
    font-size: .875em;
    &__content{
      padding: 1.25em;
    }
    .h4{
      font-size: 2em;
    }
  }

  .process,
  .help {
    &__info {
      gap: 1.25em;
      &:before{
        display: none;
      }
    }
  }

  .help{
    padding-bottom: 3em;
    ._outline-round{
      display: none;
    }
    &__wrap{
      grid-template-columns: 100%;
      margin-top: 2em;
    }
    &__info{

    }
    .m-list{
      gap: .625em;
    }
    &__img{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .help{
    &__col2,
    &__info-desc{
      display: none;
    }
  }
}