.process{
  position: relative;
  &__wrap{
    display: grid;
    grid-template-columns: 29em 43em;
    justify-content: space-between;
    align-items: center;
    gap: 1.25em;
    margin-top: -1em;
  }
  .h2{
    position: relative;
    z-index: 2;
  }
  &__info{
    gap: 2.5em;
    &:before{
      width: 82.375em;
      height: 82.375em;
    }
    &-title,
    &-desc{
      position: relative;
      z-index: 2;
    }
  }
  &__items{
    @include grid100gap(5em);
  }
  @media screen and (min-width: 993px){
    font-size: .75em;
    .container{
      padding-left: 15em;
      padding-right: 15em;
    }
    .round-btn{
      position: absolute;
      bottom: 4.5625em;
      left: 47.75em;
    }
  }

}

.process-item{
  display: flex;
  opacity: .4;
  position: relative;
  &:last-child{
    &:after,
    &:before{
      display: none;
    }
  }
  &:after,
  &:before{
    content: '';
    position: absolute;
    left: 3.125em;
    top: 6.875em;
    transform: translateX(-50%);
    border-right: 2px dashed #B2BDBE;
    height: calc(100% - 2.5em);
  }
  &:after{
    opacity: 0;
    height: calc(50% - 1.25em);
    border-color: $primary;
    z-index: 2;
  }
  &__num{
    width: 6.25em;
    height: 6.25em;
    border-radius: 50%;
    @include center;
    border: 1px dashed #B2BDBE;
    flex-shrink: 0;
    margin-right: 1.875em;
  }
  &__top{
    min-height: 6.25em;
    display: flex;
    align-items: center;
  }
  &__desc{
    margin-top: -.5em;
  }
  &._active,
  &._done{
    opacity: 1;
    .process-item__num{
      background-color: $primary;
      border-color: $primary;
      border-style: solid;
      color: $white;
    }
  }
  &._done{
    &:before{
      border-color: $primary;
    }
  }
  &._active{
    &:after{
      opacity: 1;
    }
  }
}


@media screen and (max-width: 992px){
  .process{
    &__wrap{
      grid-template-columns: 100%;
      margin-top: 1.5em;
    }
    &__info {
      gap: 1.25em;
    }
    &__items{
      gap: 2em;
    }
    .round-btn{
      width: calc(100% - 2em);
      margin: 1em auto 0;
    }
  }

  .process-item{
    &:before,
    &:after{
      left: 2em;
      top: 4.5em;
      height: calc(100% - 3em);
    }
    &:after{
      height: calc(50% - 1.5em);
    }
    &__num{
      width: 4em;
      height: 4em;
      margin-right: 1em;
    }
    &__top{
      min-height: 4em;
    }
  }
}


