@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.5em;
    height: 4em;
    border-radius: .3125em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.25em;
    }
}

.m-btn-big{
    height: 6.25em;
}

.m-btn-red-outline{
    border: 1px solid $primary;
    color: $black;
    &:hover{
        background-color: $primary;
        color: $white;
        path{
            fill: $white;
        }
    }
}

.m-btn-red{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: darken($primary,10%);
        color: $white;
    }
}

.m-btn-blue{
    background-color: $blue;
    color: $white;
    &:hover{
        background-color: darken($blue,10%);
        color: $white;
    }
}

.round-btn{
    width: 18.75em;
    height: 18.75em;
    font-weight: 700;
    text-transform: uppercase;
    border-radius: 50%;
    position: relative;
    text-align: center;
    &:hover{
        path{
            fill: $white;
        }
    }
    span{
        font-size: 1.5em;
        line-height: 120%;
        letter-spacing: .05em;
    }
    svg{
        right: 1.875em;
        bottom: 1.75em;
        width: 6.5em;
        position: absolute;
    }
}

@media screen and (max-width: 992px){
    .m-btn{
        padding: 0 1.25em;
    }

    .round-btn{
        height: 4em;
        border-radius: .3125em;
        span{
            font-size: 1em;
        }
        svg{
            width: 3em;
            position: static;
            margin: 0 0 0 .75em;
        }
    }
}