.text-center{
  text-align: center;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
.text h1,
.text h2,
.text h3,
.text h4,
.text h5,
.text h6,
{
  font-family: $font2;
  text-transform: uppercase;
  letter-spacing: .05em;
  line-height: 100%;
}

.text h1,
.h1{
  font-size: 12.5em;
}

.text h2,
.h2{
  font-size: 7.5em;
}

.text h3,
.h3{
  font-size: 3.75em;
}

.text h4,
.h4{
  font-size: 2.5em;
}

.text h5,
.h5{
  font-size: 2em;
}

.text h6,
.h6{
  font-size: 1.25em;
}

.h1{
  &__row{
    @media screen and (min-width: 993px){
      display: block;
      &:nth-child(2){
        padding-left: 1.16em;
      }
    }
  }
}

.h2{
  &__row{
    @media screen and (min-width: 993px){
      display: block;
      &:nth-child(2){
        padding-left: 1.16em;
      }
    }
  }
}

.text20{
  font-size: 1.25em;
  line-height: 140%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 120%;
}

.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.text16{
  font-size: 1em;
  line-height: 140%;
  letter-spacing: -.02em;
}

.text15{
  font-size: .9375em;
  line-height: 120%;
}

.text14{
  font-size: .875em;
  line-height: 120%;
}

.text13{
  font-size: .8125em;
  line-height: 120%;
}

.text12{
  font-size: .75em;
  line-height: 120%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.font2{
  font-family: $font2;
}

.font3{
  font-family: $font3;
}

.gray-text{
  color: $gray;
}

.red-text{
  color: $primary;
}

.blue-text{
  color: $blue;
}


@media screen and (max-width: 992px){
  .text h1,
  .h1,
  .text h2,
  .h2{
    font-size: 2.5em;
  }

  .text h3,
  .h3{
    font-size: 2em;
  }

  .text h4,
  .h4{
    font-size: 1.25em;
  }

  .text h5,
  .h5{
    font-size: 1.25em;
  }

  .text20-tablet{
    font-size: 1.25em;
  }

  .text19-tablet{
    font-size: 1.1875em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text17-tablet{
    font-size: 1.0625em;
  }

  .text16-tablet{
    font-size: 1em;
  }

  .text15-tablet{
    font-size: .9375em;
  }

  .text14-tablet{
    font-size: .875em;
  }

  .text13-tablet{
    font-size: .8125em;
  }

  .text12-tablet{
    font-size: .75em;
  }

  .text11-tablet{
    font-size: .6875em;
  }

  .text10-tablet{
    font-size: .625em;
  }

}
