.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    opacity: 0.8;
    background: #000;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #EBEFF0;
        width: 37.5em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 6.25em;
        border-radius: 1.25em;
        display: inline-block;
    }
    .close-btn{
        position: absolute;
        top: 2em;
        right: 2em;
    }
    @media screen and (min-width: 993px){
        .h3{
            font-size: 5em;
        }
    }
    .h4{
        padding: 0 .125em;
    }
    &__thank{
        @include grid100gap(1.25em);
        text-align: center;
    }
    &__icon{
        width: 9.375em;
        display: block;
        margin: 0 auto;
        max-width: 95%;
    }
}

.close-btn{
    width: 2em;
    @include center;
    img{
        width: 100%;
        display: block;
    }
}

.modal-form{
    margin-top: 1.25em;
}

.modal-thank{
    .modal__dialog{
        padding-top: 10.625em;
        padding-bottom: 10.625em;
    }
}

.anim-newSing{
    opacity: 0;
    transition-duration: 1.4s;
    &--transform{
        transform: translateY(30px);
    }
}
.anim-newSing.in-view{
    opacity: 1;
    &.anim-newSing--transform{
        transform: translate(0);
    }
}




@media screen and (max-width: 992px){
    .modal{
        &__dialog {
            width: 25em;
            padding: 4em 1.25em;
            border-radius: .625em;
        }
        .close-btn{
            top: 1em;
            right: 1em;
        }
        &__icon{
            width: 7em;
        }
        &__thank{
            gap: .625em;
        }
    }

    .close-btn{
        width: 1.5em;
    }

    .modal-form{
        gap: .625em;
    }

    .modal-thank{
        .modal__dialog{
            padding-top: 6em;
            padding-bottom: 6em;
        }
    }

}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {

        }
    }
}
