.process--sale{
  z-index: 3;
  position: relative;
  @media screen and (min-width: 993px){
    .process__wrap{
      margin-top: 3.75em;
      grid-template-columns: 42.8125em 32.5em;
    }
    .process__col2{
      order: -1;
    }
    .round-btn{
      bottom: -6.25em;
    }
  }
}

.referall-main{
  padding: 11.25em 0 3.375em;
  &__inner{
    position: relative;
  }
  &__top{
    position: relative;
  }
  .h1{
    max-width: 5.5em;
  }
  &__badge{
    position: absolute;
    height: 5em;
    padding: 0 2.5em;
    background-color: $blue;
    color: $white;
    border-radius: .625em;
    display: flex;
    align-items: center;
    bottom: -3.75em;
    left: 29em;
    transform: rotate(-3.282deg);
  }
  &__bottom{
    display: flex;
    margin-top: 1.25em;
    position: relative;
    z-index: 2;
  }
  &__col{
    width: 25.5em;
    margin-right: 26.25em;
    flex-shrink: 0;
    padding-top: 7.5em;
  }
  &__media{
    position: absolute;
    width: 35em;
    bottom: 6em;
    right: 0;
  }
  &__img{
    @include imgCover(114%);
    border-radius: 1.25em;;
  }
  &__link{
    @include center;
    position: absolute;
    left: 5em;
    bottom: -2.75em;
    width: 8.75em;
    height: 8.75em;
    border-radius: 50%;
    img{
      width: 3.75em;
    }
  }
  @media screen and (min-width: 993px){
    font-size: .75em;
    min-height: $vh;
    .h1{
      max-width: 6.5em;
    }
    &__bottom{
      margin-top: 7em;
    }
    .round-btn{
      width: 25em;
      height: 25em;
      svg{
        right: 3.125em;
        bottom: 3.625em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .referall-main{
    padding: 7em 0 2em;
    .h1{
      max-width: 100%;
    }
    &__media{
      position: relative;
      bottom: unset;
      right: unset;
      max-width: 25em;
      width: 100%;
      margin-bottom: 1.25em;
    }
    &__link{
      display: none;
    }
    &__bottom{
      @include grid100gap(1em);
      margin-top: 1.5em;
    }
    &__col{
      width: 100%;
      padding: 0;
      margin: 0;
    }
    .round-btn{
      width: 100%;
    }
    &__badge{
      display: inline-flex;
      position: static;
      height: 3.25em;
      padding: 0 1.25em;
      margin-top: 1em;
      transform: unset;
    }
  }
}


@media screen and (max-width: 576px){
  .referall-main{
    .round-btn{
      span{
        font-size: 1em;
      }
      svg{
        display: none;
      }
    }
  }
}