.text{
  h1{
    margin-bottom: .125em;
  }
  h2{
    margin-bottom: .25em;
  }
  h3{
    margin-bottom: .25em;
  }
  h4,
  h5,
  h6{
    margin-bottom: .5em;
  }
  ol,
  ul{
    @include lastM(0 0 1.25em);
    @include grid100gap(.625em);
  }
  li,
  p{
    font-size: 1.25em;
    line-height: 140%;
  }
  p{
    @include lastM(0 0 1em);
  }
  ol {
    counter-reset: list;
    li {
      counter-increment: list;
      &:before {
        content: counter(list) '. ';
      }
    }
  }
  ul{
    li{
      position: relative;
      padding-left: 1.2em;
      &:before{
        content: '';
        position: absolute;
        top: .6em;
        left: 0;
        width: .2em;
        height: .2em;
        background-color: $primary;
      }
    }
  }
  hr{
    height: 1.25em;
    display: block;
    opacity: 0;
    margin: 0;
  }
}



@media screen and (max-width: 992px){
  .text{
    li,
    p{
      font-size: 1em;
    }
    ol,ul{
      @include lastM(0 0 1em);
    }
    hr{
      height: .625em;
    }
  }
}


