.vacancies{
  padding: 7.75em 0 3.75em;
  background: #EBEFF0;
  flex-grow: 1;
  .h2{
    text-transform: unset;
  }
  &__top{
    position: relative;
    z-index: 3;
  }
  &__grid{
    margin-top: 2.5em;
  }
}

.vacancies-group{
  margin-top: 2.5em;
  &__top{
    font-size: .75em;
  }
}

.filter-select{
  position: relative;
  z-index: 3;
  &._open{
    .filter-select__btn{
      transform: rotate(-180deg);
    }
    .filter-select__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__label{
    color: #7B7B7B;
    font-size: 1em;
    line-height: 125%;
    display: block;
    margin-bottom: .69em;
  }
  &__show{
    z-index: 3;
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    color: $primary;
  }
  &__btn{
    transition: all .3s;
    width: 3.75em;
    height: 3.75em;
    svg{
      width: 100%;
    }
  }
  &__dropdown{
    border-radius: 1.25em;
    position: absolute;
    top: 100%;
    margin-top: .25em;
    left: 0;
    width: 100%;
    background-color: $white;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    z-index: 2;
    padding: 1em 1.5em;
    box-shadow: 0 1px 10px rgba(0,0,0,.05);
  }
  &__options{
    max-height: 25em;
    overflow-y: auto;
    padding: .25em 1.25em .25em 0;
    @include grid100gap(.625em);
  }
  &__option{
    position: relative;
    input {
      position: absolute;
      opacity: 0;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      z-index: 2;
      cursor: pointer;
      &:checked~label{
        color: $primary;
      }
    }
    label{
      transition: all .3s;
      font-size: 3em;
      text-transform: uppercase;
      font-family: $font2;
    }
  }
}

.vacancy-card3{
  padding: 2.5em;
  background-color: $white;
  border-radius: 1.25em;
  &__img{
    height: 6.25em;
    display: flex;
    align-items: center;
    margin-bottom: 1.5em;
    img{
      max-width: 100%;
      max-height: 100%;
      border-radius: .625em;
    }
  }
  &__info{
    @include grid100gap(.625em);
  }
  &__tags{
    display: flex;
    flex-wrap: wrap;
    margin-top: .875em;
    width: calc(100% + .875em);
  }
  &__tag{
    margin: .625em .625em 0 0;
    padding: 0 .625em;
    height: 2.5em;
    display: flex;
    align-items: center;
    background: #EBEFF0;
    border-radius: .625em;
  }
  &__desc{
    margin-top: 1.5em;
    b{
      font-weight: 600;
    }
    p{
      @include lastM(0 0 .5em);
    }
  }
}


@media screen and (max-width: 992px){
  .vacancies{
    padding: 7em 0 2em;
    &__grid{
      margin-top: 1.5em;
    }
  }

  .filter-select{
    &__btn{
      width: 2em;
      height: 2em;
    }
    &__dropdown{
      border-radius: .625em;
    }
    &__option label{
      font-size: 1.5em;
    }
  }

  .vacancy-card3{
    padding: 1.25em;
    &__img{
      height: 5em;
    }
  }
}


@media screen and (max-width: 576px){
  .vacancies{
    &__top{
      display: block;
    }
  }

  .vacancy-card3{
    font-size: .875em;
  }
}