.main{
  padding: 11.25em 0 3.75em;
  position: relative;
  .consult__round3{
    top: -31em;
    right: -43em;
    transform: rotate(31.367deg);
  }
  &__top{
    padding-left: 3em;
    padding-bottom: 7em;
    position: relative;
    z-index: 2;
  }
  &__desc{
    margin-left: 13.625em;
    max-width: 24.75em;
    margin-top: 2.5em;
    b{
      font-weight: 600;
    }
  }
  &__items{
    position: relative;
    z-index: 3;
    display: grid;
    grid-template-columns: repeat(4,25%);
  }
  @media screen and (min-width: 993px){
    font-size: .75em;
    .h1{
      max-width: 8em;
    }
    .round-btn{
      width: 25em;
      height: 25em;
      right: 28.75em;
      bottom: -1.5em;
      position: absolute;
      padding: 3.75em;
      z-index: 2;
      svg{
        right: 3.125em;
        bottom: 3.625em;
      }
    }
  }
}

.main-slider{
  position: absolute;
  right: -8.125em;
  bottom: 4.5em;
  width: 40.375em;
  overflow: hidden;
  .swiper{
    &-container{
      width: 36.875em;
    }
    @media screen and (min-width: 993px){
      &-slide{
        padding-right: 1.875em;
        &-next{
          .vacancy-card,
          .main-slider__img{
            transform: rotate(9deg) scale(.43) translate(-21em,6em);
          }
        }
      }
    }
  }
  &__img{
    transition: all .3s;
    @include imgCover(114%);
    border-radius: 1.25em;
  }
}

.main-card{
  background: rgba(255, 255, 255, 0.30);
  backdrop-filter: blur(10px);
  padding: 2.875em 5em;
  border-right: 2px solid $white;
  &:last-child{
    border-right: none;
  }
  &__icon{
    height: 5em;
    margin-bottom: 1.25em;
    img{
      width: auto;
      height: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 7em 0 1.5em;
    ._outline-round{
      display: none;
    }
    &__top{
      padding: 0;
    }
    &__desc{
      margin: 1em 0 0;
      max-width: 100%;
    }
    .round-btn{
      width: 100%;
      margin-top: 1em;
    }
    &__items{
      overflow-x: auto;
      display: flex;
      padding-bottom: .5em;
    }
  }

  .main-slider{
    position: static;
    overflow: unset;
    margin-top: 1.5em;
    .swiper{
      &-container{
        width: calc(100% + 1.25em);
      }
      &-slide{
        width: 20em;
      }
    }
  }

  .main-card{
    padding: 1.25em;
    border-top: 2px solid $white;
    border-bottom: 2px solid $white;
    width: 16.75em;
    flex-shrink: 0;
    &__icon{
      height: 4em;
      margin-bottom: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .main-slider{
    .swiper{
      &-slide{
        width: 18em;
      }
    }
  }
}