@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
._flex,
._flex-between,
._flex-center{
  display: flex;
  align-items: center;
  &._start{
    align-items: unset;
  }
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}

._mt60{
  margin-top: 3.75em;
}

._mt48{
  margin-top: 3em;
}

._mt32{
  margin-top: 2em;
}

._mt24{
  margin-top: 1.5em;
}

._mt18{
  margin-top: 1.125em;
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.m-section{
  padding: 8.75em 0;
  &--gray{
    background: #EBEFF0;
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.2);
    pointer-events: none;
    border-radius: inherit;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 4em;
    height: 4em;
    border-radius: 50%;
    @include center;
    &:hover{
      path{
        fill: $white;
      }
    }
    svg{
      width: 1.5em;
    }
  }
}


@media screen and (max-width: 992px){
  .m-section{
    padding: 3em 0;
  }

  ._mt60{
    margin-top: 1.25em;
  }

  ._mt30-tablet{
    margin-top: 1.875em;
  }
}


