.footer{
  background: #EBEFF0;
  padding: 4.375em 0;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__col2{
    width: 20em;
  }
  &__logo{
    display: block;
    width: 16.25em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.footer-nav{
  width: 36em;
  margin-bottom: -1.25em;
  ul{
    column-count: 2;
    column-gap: 4em;
  }
  li{
    margin-bottom: 1.25em;
  }
  a{
    font-size: 1em;
    font-weight: 500;
    line-height: 140%;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 3em 0;
    &__inner{
      display: grid;
      grid-template-columns: repeat(2, calc(50% - .75em));
      gap: 1.5em;
    }
    &__col2{
      width: 100%;
      margin-bottom: -.75em;
    }
    &__logo{
      width: 12em;
    }
  }

  .footer-nav{
    width: auto;
    margin: 0;
    ul{
      column-count: 1;
    }
    li{
      margin-bottom: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__inner{
      grid-template-columns: 100%;
    }
  }
}
