.advantages2 {
  &__subtitle {
    margin-top: 3.75em;
  }
  .advantages2__cards2 {
    grid-template-columns: repeat(4, calc(25% - 3.75em / 4));
  }
}

.bonuses {
  position: relative;
  overflow: hidden;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: -11.875em;
    background: #e3eaeb;
    width: 75em;
    height: 75em;
    border-radius: 50%;
  }
  .container {
    position: relative;
    z-index: 2;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 3.125em;
    margin-top: 3.75em;
  }
  &__subtitle {
    max-width: 38.4375em;
    p {
      font-family: "Coolvetica Condensed", sans-serif;
      font-size: 5em;
      line-height: 100%;
      text-transform: uppercase;
      letter-spacing: 0.05em;
      span {
        color: #E74C3D;
      }
    }
  }
  .m-list {
    max-width: 42.5em;
    width: 100%;
  }
}

@media screen and (max-width: 992px) {
  .bonuses {
    &:before {
      display: none;
    }
    &__wrap {
      flex-direction: column;
      gap: 1.25em;
      align-items: start;
      margin-top: 1.25em;
    }
    &__subtitle {
      max-width: 100%;
      p {
        font-size: 1.875em;
      }
    }
  }

  .advantages2 {
    &__subtitle {
      margin-top: 1.25em;
    }
    .advantages2__cards2 {
      grid-template-columns: repeat(3, calc(100% / 3 - 2.5em / 3));
    }
  }

  .advantage2-card {
    &__icon {
      width: 3.125em;
      height: 3.125em;
    }
  }
}

@media screen and (max-width: 576px) {
  .advantages2 {
    .advantages2__cards2 {
      grid-template-columns: 100%;
    }
  }
}