.about{
  padding-bottom: 1.875em;
  position: relative;
  ._outline-round{
    top: 9.25em;
    left: -60.5em;
    transform: rotate(-32.642deg);
  }
  .container{
    position: relative;
    z-index: 4;
  }
  .h2{
    position: relative;
    z-index: 4;
  }
}

.about-card{
  padding: 3.5em 2.5em;
  background-color: $white;
  border-radius: 1.25em;
  text-align: center;
  @include center;
  flex-direction: column;
  &__title{
    font-size: 5em;
  }
  &__desc{
    margin-top: .5em;
  }
}

.about2{
  padding-top: 1.875em;
  overflow: hidden;
  ._outline-round{
    top: 11em;
    right: -15.625em;
    transform: rotate(-32.642deg);
  }
  &__inner{
    padding: 17.5em 0 16.625em;
    position: relative;
  }
  .profit__info{
    margin-top: 0;
    padding: 5em;
  }
  &__item1{
    top: 26.625em;
    left: 13.25em;
  }
  &__item2{
    top: 0;
    left: 36em;
  }
  &__item3{
    top: 2.5em;
    right: 30.75em;
  }
  &__item4{
    top: 24.25em;
    right: 13.25em;
  }
  &__item5{
    bottom: 0;
    right: 29em;
  }
  &__item6{
    bottom: 3.125em;
    left: 29.6875em;
  }
}


@media screen and (max-width: 992px){
  .about{
    ._outline-round{
      display: none;
    }
    &__grid{
      @include fullScroll;
      display: flex;
      padding-bottom: .5em;
      gap: 0;
    }
  }

  .about-card{
    padding: 1.25em;
    border-radius: .625em;
    width: 14em;
    flex-shrink: 0;
    margin-right: 1em;
    &:last-child{
      margin-right: 0;
    }
    &__title{
      font-size: 2em;
    }
  }

  .about2{
    padding: 1em 0 2em;
    display: none;
    ._outline-round{
      display: none;
    }
    &__inner{
      min-height: unset;
      display: grid;
      grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
      gap: 1em;
      padding: 1em;
    }
    .profit__item{
      top: unset;
      left: unset;
      bottom: unset;
      right: unset;
      min-height: unset;
    }
  }
}


@media screen and (max-width: 576px){
  .about2{
    &__inner{
      grid-template-columns: 100%;
    }
  }
}