.header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
  padding: 1.25em 0;
  &__inner{

  }
  &__logo{
    width: 12.25em;
    display: block;
    flex-shrink: 0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    img{
      width: 100%;
      display: block;
    }
  }
}

.burger{
  display: none;
}

.menu{
  &__scroll{
    &:before{
      content: '';
      width: 12.25em;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  font-family: $font3;
  font-weight: 500;
  &__link{
    font-size: 1em;
    line-height: 140%;
    margin-right: 3.75em;
    &:last-child{
      margin: 0;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &__inner{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__logo{
      width: 12em;
      z-index: 11;
      position: relative;
      top: unset;
      transform: unset;
    }
    &__btn{
      margin: 2em 0 0;
      width: 100%;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .menu{
    transform: unset;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $white;
    padding: 6em 0 0;
    z-index: 10;
    &__scroll{
      padding: 1em;
      display: flex;
      flex-direction: column;
      justify-content: unset;
      overflow-y: auto;
      height: 100%;
    }
    &__btn{
      margin: 2em 0 0;
      width: 100%;
      height: 3em;
    }
  }

  .burger{
    z-index: 11;
    position: relative;
    width: 1.5em;
    height: 1.5em;
    display: flex;
    margin-left: 1em;
    img{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
      width: 90%;
    }
  }

  .nav{
    flex-direction: column;
    &__link{
      margin: 0 0 1em;
    }
  }
}


