.hire{
  position: relative;
  color: $white;
  &__top{
    position: relative;
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #141414;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      opacity: .2;
    }
  }
}

.consult2{
  @media screen and (min-width: 993px){
    .consult__top{
      padding-left: 36em;
    }
  }
  ._outline-round{
    display: none;
  }
}

.advantages{
  padding: 8.75em 0 6.5em;
  position: relative;
  z-index: 2;
  &__desc{
    padding-left: 8.75em;
  }
}

.advantage-card{
  &__icon{
    height: 6.25em;
    img{
      height: 100%;
      display: block;
    }
  }
  &__info{
    margin-top: 1.25em;
    @include grid100gap(1.25em);
  }
}

.test{
  z-index: 3;
  position: relative;
  &:after{
    backdrop-filter: blur(5px);
  }
  .container{
    position: relative;
    z-index: 2;
  }
  &__inner{
    padding-left: 50.5em;
  }
  &__items{
    @include grid100gap(2.75em);
  }
  &__info{
    z-index: 3;
    position: absolute;
    top: -6.875em;
    left: -38em;
    background-color: $primary;
    color: $white;
    border-radius: 312em;
    display: flex;
    align-items: center;
    padding: 7.625em 7.625em 7.625em 46em;
    width: 92.25em;
    height: 92.25em;
    &-title{
      font-size: 5em;
    }
  }
}

.test-item{
  @include grid100gap(.9375em);
}


@media screen and (max-width: 992px){
  .advantages{
    padding: 3em 0;
    &__desc{
      padding-left: 0;
    }
    &__grid{
      @include fullScroll;
      display: flex;
      padding-bottom: .5em;
      gap: 0;
    }
  }

  .advantage-card{
    width: 14em;
    flex-shrink: 0;
    margin-right: 1em;
    &:last-child{
      margin-right: 0;
    }
    &__icon{
      height: 5em;
    }
    &__info{
      margin-top: .75em;
      gap: .75em;
    }
  }

  .test{
    &__inner{
      padding-left: 0;
    }
    &__items{
      @include grid100gap(1.25em);
    }
    &__info{
      position: relative;
      left: 0;
      top: 0;
      border-radius: 0;
      padding: 2em 1em;
      width: 100%;
      height: auto;
      margin: -3em 0 1.5em;
      &-title{
        font-size: 2em;
      }
    }
  }

  .test-item{
    gap: .5em;
    .text20{
      font-size: .875em;
    }
  }
}


