.vacancy-main{
  @media screen and (min-width: 993px){
    .main__top{
      padding-left: 0;
    }
    .main__desc{
      margin-left: 8.5em;
    }
    .consult__round3{
      top: -7em;
      right: -48.625em;
    }
    .round-btn{
      right: 51.125em;
      bottom: 13.375em;
      z-index: 4;
      span{
        width: 8em;
      }
    }
  }
}

.vacancy-card{
  align-items: flex-start;
  padding: 2.5em 4.75em;
  background-color: $white;
  border-radius: 1.25em;
  height: 100%;
  display: flex;
  flex-direction: column;
  height: 100%;
  transition: all .3s;
  &__title{
    font-size: 5em;
  }
  &__price{
    margin-top: .625em;
  }
  &__experience{
    display: inline-flex;
    align-items: center;
    border-radius: .625em;
    background: #EBEFF0;
    height: 2.5em;
    margin-top: 1.5em;
    padding: 0 1.25em;
  }
  &__info{
    margin-top: 3.5em;
    img{
      width: 5.125em;
      margin-right: 1.25em;
      border-radius: .625em;
    }
  }
  &__street{
    margin-top: 1.625em;
  }
  &__nav{
    padding-top: 3.5em;
    margin-top: auto;
    width: 100%;
  }
  &__btn{
    width: 100%;
  }
}

.vacancy-collection{
  padding: 1.875em 0 4.375em;
  &__items{
    margin-top: 1.25em;
  }
}

.vacancy-card2{
  padding: 2.5em;
  .vacancy-card__nav{
    padding-top: 1.5em;
  }
  .vacancy-card__desc{
    margin-top: 1.5em;
  }
}

.find{
  padding: 4.375em 0 1.875em;
}

.candidate-card2{
  .candidate-card__content{
    text-align: left;
  }
}


@media screen and (max-width: 992px){
  .vacancy-main{

  }

  .vacancy-card{
    font-size: 1em;
    padding: 1.25em;
    &__title{
      font-size: 2.75em;
    }
    &__info{
      margin-top: 2em;
    }
    &__nav{
      padding-top: 2em;
    }
  }

  .vacancy-collection{
    padding: 1.5em 0 3em;
    &__items{
      margin-top: 1.25em;
    }
    .swiper-slide{
      width: 20em;
    }
  }

  .vacancy-card2{
    .vacancy-card__title2{
      font-size: 2.75em;
    }
  }
}


