.partners {
  &__title {
    position: relative;
    z-index: 2;
  }
  &__subtitle {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: center;
    margin-top: 5em;
  }
  &__cards {
    position: relative;
    margin-top: -1.25em;
  }
  ._outline-round {
    position: static;
    transform: rotate(-9deg);
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto 0 auto;
  }
}

.partner-card {
  position: absolute;
  z-index: 1;
  width: 12.5em;
  height: 12.5em;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: #fff;
  border-radius: 50%;
  padding: 1em;
  &--blue {
    width: 25em;
    height: 25em;
    background: #3597DA;
  }
  img {
    max-width: 8.75em;
    min-width: 8.75em;
    max-height: 100%;
  }
  &__text {
    max-width: 17.5em;
    color: #fff;
    margin: auto;
  }
  &:nth-child(1) {
    top: 5em;
    left: 18em;
  }
  &:nth-child(2) {
    top: 10em;
    left: 45%;
    transform: translate(-50%, 0);
  }
  &:nth-child(3) {
    top: 4em;
    right: 38%;
    transform: translate(50%, 0);
  }
  &:nth-child(4) {
    top: 3.125em;
    right: 9.375em;
  }
  &:nth-child(5) {
    top: 50%;
    left: 3.125em;
    transform: translate(0, -50%);
  }
  &:nth-child(6) {
    top: 18.75em;
    left: 21.875em;
  }
  &:nth-child(7) {
    top: 43%;
    right: 39%;
    transform: translate(50%, -50%);
  }
  &:nth-child(8) {
    left: 16em;
    bottom: 6em;
  }
  &:nth-child(9) {
    left: 44%;
    bottom: 13em;
    transform: translate(-50%, 0);
  }
  &:nth-child(10) {
    right: 35%;
    bottom: 5em;
    transform: translate(50%, 0);
  }
  &:nth-child(11) {
    right: 9.375em;
    bottom: 22em;
  }
  &:nth-child(12) {
    right: 14em;
    bottom: 0;
  }
}

.advantages2 {
  &__cards {
    display: grid;
    grid-template-columns: repeat(6, calc(100% / 6 - 6.25em / 6));
    gap: 1.25em;
    margin-top: 3.75em;
  }
}

.advantage2-card {
  background: #FBFCFC;
  border-radius: 0.625em;
  padding: 3.75em 1.25em;
  &__icon {
    width: 6.25em;
    height: 6.25em;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    img {
      height: 100%;
      width: auto;
    }
  }
  &__text {
    margin-top: 0.625em;
  }
}

.info {
  padding-bottom: 4.375em;
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 3.125em;
  }
  &__title {
    span {
      display: block;
      color: #E74C3D;
    }
  }
  &__text {
    max-width: 51.25em;
    p {
      span {
        color: #C0C9CB;
      }
    }
  }
}

.advantages3 {
  padding-top: 4.375em;
  &__title {
    letter-spacing: 0.04em;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
    margin-top: 3.75em;
  }
}

.advantage3-card {
  background: #fff;
  border-radius: 1.25em;
  padding: 2.5em;
  &__icon {
    width: 6.25em;
    img {
      width: 100%;
    }
  }
  &__title {
    margin-top: 1.125em;
  }
  &__text {
    margin-top: 1.125em;
  }
}


@media screen and (max-width: 992px) {
  .partners {
    &__subtitle {
      margin-top: 1.25em;
    }
    &__cards {
      display: grid;
      grid-template-columns: repeat(3, calc(100% / 3 - 2.5em / 3));
      gap: 1.25em;
      padding: 0 1em;
      margin: 1.25em 0 0 0;
    }
    ._outline-round {
      display: none;
    }
  }

  .partner-card {
    position: static;
    transform: unset !important;
    width: 14.5em;
    height: 14.5em;
  }

  .advantages3,
  .advantages2 {
    &__cards {
      @include fullScroll;
      display: flex;
      padding-bottom: .5em;
      gap: 0;
      margin-top: 1.25em;
    }
  }

  .advantage3-card,
  .advantage2-card{
    width: 16em;
    flex-shrink: 0;
    margin-right: 1em;
    &:last-child{
      margin-right: 0;
    }
  }

  .info {
    padding-bottom: 1.5em;
    &__inner {
      flex-direction: column;
      gap: 1.25em;
    }
    &__text {
      max-width: 100%;
    }
  }

  .advantages3 {
    padding-top: 1.5em;
  }

  .advantage3-card {
    padding: 1.5em;
    &__icon {
      width: 3.125em;
    }
  }
}

@media screen and (max-width: 576px) {
  .partners {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
    }
  }

  .partner-card {
    width: 9.63em;
    height: 9.63em;
    img {
      max-width: 6em;
      min-width: unset;
    }
  }

  .advantages2 {
    &__cards {
      grid-template-columns: 100%;
    }
  }

  .advantages3 {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}