.experts{
  overflow: hidden;
}

.slider{
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      padding-right: 1.25em;
    }
  }
}

.swiper-nav{
  display: flex;
}

.swiper-button{
  width: 6.25em;
  height: 6.25em;
  border-radius: 50%;
  padding: 0;
  svg{
    width: 1.25em;
  }
}

.candidates-slider,
.business-slider,
.experts-slider{
  .swiper{
    &-nav{
      position: absolute;
      right: 0;
      bottom: 100%;
      margin-bottom: 2.5em;
    }
    &-button{
      position: static;
      margin: 0;
    }
  }
}

.candidates-slider,
.experts-slider{
  .swiper{
    &-slide{
      width: 25%;
    }
  }
}

.faq{
  &__items{
    @include grid100gap(.625em);
  }
}

.qa-card{
  padding: 1.25em 2.5em;
  border-radius: 1.25em;
  background: $white;
  &._active{
    .qa-card__btn img{
      transform: rotate(-45deg);
    }
  }
  &__show{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 6.25em;
  }
  &__btn{
    margin-left: 1em;
    width: 2.5em;
    @include center;
    flex-shrink: 0;
    img{
      transition: all .3s;
      width: 100%;
    }
  }
  &__hidden{
    margin-top: -.625em;
    padding-top: 1.25em;
    display: none;
    border-top: 1px solid #E3E3E3;
  }
}

.delegate{
  position: relative;
  overflow: hidden;
  ._outline-round{
    transform: rotate(-56.886deg);
    top: 7.25em;
    right: -52.375em;
  }
  ._mt60{
    margin-top: 1.25em;
  }
}

.delegate-table{
  table{
    width: 100%;
    border-spacing: 0;
  }
  th,td{
    width: calc(100% / 3);
  }
  th{
    font-weight: 400;
  }
  td{
    background: rgba(235, 239, 240, 0.20);
    border-bottom: 1px solid #E3E3E3;
  }
  &__th{
    height: 6em;
    @include center;
    border-radius: 1.25em;
  }
  tr{
    &:first-child{
      td{
        padding-top: .3125em;
        &:first-child{
          border-radius: 1.25em 0 0 0;
        }
        &:last-child{
          border-radius: 0 1.25em 0 0;
        }
      }
    }
    &:last-child{
      td{
        border-bottom: none;
        &:first-child{
          border-radius: 0 0 0 1.25em;
        }
        &:last-child{
          border-radius: 0 0 1.25em 0;
        }
      }
    }
  }
  &__td{
    padding: .5em 5em;
    min-height: 6.75em;
    display: flex;
    align-items: center;
    &._dark{
      position: relative;
      z-index: 2;
      border-radius: 1.25em;
    }
  }
  ._dark{
    background-color: $black;
    color: $white;
  }
}


@media screen and (max-width: 992px){
  .swiper-button{
    width: 4em;
    height: 4em;
  }

  .experts-slider{
    .swiper{
      &-slide{
        width: 16.5em;
      }
      &-nav{
        display: none;
      }
    }
  }

  .qa-card{
    padding: .625em 1.25em;
    border-radius: .625em;
    &__show{
      min-height: 4em;
    }
    &__btn{
      width: 1.75em;
    }
    &__hidden{
      padding-bottom: .625em;
      padding-top: .625em;
      margin-top: .625em;
    }
  }

  .delegate{
    ._outline-round{
      display: none;
    }
  }

  .delegate-table{
    &__th{
      height: 5em;
      border-radius: .625em;
    }
    th,td{
      &:nth-child(1){
        width: 25%;
      }
    }
    tr{
      &:first-child{
        td{
          &:first-child{
            border-radius: .625em 0 0 0;
          }
          &:last-child{
            border-radius: 0 .625em 0 0;
          }
        }
      }
      &:last-child{
        td{
          &:first-child{
            border-radius: 0 0 0 .625em;
          }
          &:last-child{
            border-radius: 0 0 .625em 0;
          }
        }
      }
    }
    &__td{
      padding: .625em 1em;
      min-height: 4.5em;
      height: 100%;
      &._dark{
        border-radius: .625em;
      }
    }
    .text20{
      font-size: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .experts-slider{
    .swiper{
      &-slide{
        width: 19.5em;
      }
    }
  }

  .delegate-table{
    @include fullScroll;
    table{
      font-size: .75em;
      min-width: 40em;
    }
    th,td{
      &:nth-child(1){
        width: 6em;
      }
      &:nth-child(2){
        width: 8em;
      }
    }
  }
}