.steps{
  position: relative;
  z-index: 2;
  &__wrap{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  ._outline-round{
    top: 1.25em;
    right: -2.75em;
    transform: rotate(14.877deg);
  }
  .container{
    z-index: 2;
    position: relative;
  }
  &__inner{
    min-height: 36em;
    position: relative;
  }
  &__line{
    position: absolute;
    width: calc(100% + 16.25em);
    left: -8.125em;
    top: 13em;
  }
  &__items{
    position: relative;
    z-index: 2;
  }
  .round-btn{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: -6.875em;
  }
}

.step-item{
  width: 25em;
  max-width: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__desc{
    margin-top: 1.25em;
  }
  &__num{
    margin: 3.75em auto 0;
    width: 5em;
    height: 5em;
    @include center;
    background-color: $primary;
    color: $white;
    border-radius: 50%;
  }
  @media screen and (min-width: 993px){
    &:nth-child(2){
      position: absolute;
      top: 13.3125em;
      left: 26.25em;
      .step-item__num{
        margin: 0 0 3.75em;
        order: -1;
      }
    }
    &:nth-child(3){
      position: absolute;
      top: 7.625em;
      left: 52.5em;
    }
    &:nth-child(4){
      position: absolute;
      top: 19.5em;
      right: 0;
      .step-item__num{
        margin: 0 0 3.75em;
        order: -1;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .steps{
    ._outline-round{
      display: none;
    }
    &__inner{
      min-height: unset;
    }
    &__line{
      display: none;
    }
    &__items{
      @include grid100gap(1.25em);
    }
    .round-btn{
      transform: unset;
      position: relative;
      z-index: 2;
      left: unset;
      bottom: unset;
      width: calc(100% - 2em);
      margin: 1.25em auto 0;
    }
  }

  .step-item{
    align-items: flex-start;
    flex-direction: unset;
    text-align: left;
    &__num{
      flex-shrink: 0;
      order: -1;
      margin: 0 1em 0 0;
      width: 4em;
      height: 4em;
    }
    &__desc{
      font-size: .75em;
      margin-top: 1em;
    }
  }
}


